import React from "react";

interface SavingsPredictionProps {
  savings: number;
}

const SavingsPrediction = ({ savings }: SavingsPredictionProps) => {
  return (
    <div>
      <h4>Predicted Savings</h4>
      <p>
        Based on your current expenses and income, we predict that you will save
        approximately ${savings} this month.
      </p>
    </div>
  );
};

export default SavingsPrediction;
