// pages/index.tsx
import React, { useEffect, useState } from "react";
import Sidebar from "../components/Sidebar";
import ExpenseChart from "../components/ExpenseChart";
import IncomeChart from "../components/IncomeChart";
import SavingsPrediction from "../components/SavingsPrediction";
import WeeklySummary from "../components/WeeklySummary";
import {
  fetchTransactions,
  fetchIncome,
  fetchExpenses,
  fetchSavingsPrediction,
} from "../lib/api";

const Index = () => {
  const [transactions, setTransactions] = useState([]);
  const [income, setIncome] = useState([]);
  const [expenses, setExpenses] = useState([]);
  const [savings, setSavings] = useState<number | null>(null);

  useEffect(() => {
    const getData = async () => {
      try {
        // Fetch data from API
        const transactionsData = await fetchTransactions();
        const incomeData = await fetchIncome();
        const expensesData = await fetchExpenses();
        const savingsData = await fetchSavingsPrediction();

        // Set data to state
        setTransactions(transactionsData);
        setIncome(incomeData);
        setExpenses(expensesData);
        setSavings(savingsData);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    getData();
  }, []);

  return (
    <div style={{ display: "flex" }}>
      {/* Sidebar navigation */}
      <Sidebar />

      {/* Main content */}
      <div style={{ marginLeft: "250px", padding: "20px", width: "100%" }}>
        <h1>Personal Finance Dashboard</h1>

        {/* Expense and Income Charts */}
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <div style={{ width: "48%" }}>
            <h3>Expense Chart</h3>
            <ExpenseChart expenses={expenses} />
          </div>
          <div style={{ width: "48%" }}>
            <h3>Income Chart</h3>
            <IncomeChart income={income} />
          </div>
        </div>

        {/* Savings Prediction and Weekly Summary */}
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            marginTop: "20px",
          }}
        >
          <div style={{ width: "48%" }}>
            <h3>Savings Prediction</h3>
            {savings !== null ? (
              <SavingsPrediction savings={savings} />
            ) : (
              <p>Loading savings prediction...</p>
            )}
          </div>
          <div style={{ width: "48%" }}>
            <h3>Weekly Summary</h3>
            <WeeklySummary transactions={transactions} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Index;
