// components/IncomeChart.tsx
import React from "react";
import { PieChart } from "react-minimal-pie-chart";

interface Income {
  source: string;
  amount: number;
}

interface IncomeChartProps {
  income: Income[];
}

const IncomeChart = ({ income }: IncomeChartProps) => {
  const data = income.map((incomeSource) => ({
    title: incomeSource.source,
    value: incomeSource.amount,
    color: "#3498DB",
  }));

  return <PieChart data={data} />;
};

export default IncomeChart;
